export default {
  data() {
    return {
      login: {
        loading: false,
        phone: "",
        password: "",
      },
    };
  },
  methods: {
    defaultHandlerLogin: function () {
      this.login.loading = true;
      setTimeout((e) => {
        uni.request({
          url: "http://127.0.0.1:3000/admin/login",
          method: "GET",
          data: {
            userphone: this.login.phone,
            userpassword: this.login.password,
          },
          success: (res) => {
            console.log(res);
            // state.studentsdata = res.data
            if (res.success) {
              // this.isregistered = true;
              uni.setStorage({
                key: "user",
                data: res.token,
              });
              uni.switchTab({
                url: "/pages/index/index",
              });
            } else {
              // this.isregistered = false;
              uni.showModal({
                title: "提示",
                content: "登录失败，密码或者账号有问题",
                success: function (res) {
                  if (res.confirm) {
                    console.log("用户点击确定");
                  } else if (res.cancel) {
                    console.log("用户点击取消");
                  }
                },
              });
            }
            // 你是哪个路径 流弄换哪个路径

            // context.commit('getstudentsdata', res.data)
          },
          fail: () => {},
          complete: () => {},
        });

        this.login.loading = false;
      }, 1500);
      console.log(JSON.stringify(this.login));
    },
    BindInput: function (e) {
      var dataval = e.currentTarget.dataset.val;
      this.login[dataval] = e.detail.value;
      console.log(this.login);
    },
  },
};
